*{
  box-sizing: border-box;
}
.App {
  background: url("./img/Ativo-4.svg");
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100vw;
}

.blue{
  background: blue;
  height: 100vh;
}
.yellow{
  background: yellow;
  height: 100vh;
}
.pink{
  background: pink;
  height: 100vh;
}
.blue-col{
  background: blue;
}
.yellow-col{
  background: yellow;
  width: 100%;
}
.pink-col{
  background: pink;
  width: 100%;
}
/* width */
::-webkit-scrollbar {
  width: 0px;
}
/* Track */
::-webkit-scrollbar-track {
  background: transparent; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: transparent; 
}