.pcs_1{
    width: 100%;
    border-radius: 1.4rem;
    border: 5px solid #393351;
}
.pcs_2{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 80%;
    margin-left: 3.5rem;

    & .pcs_9{
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        font-size: 3rem;
        border-radius: 1.4rem;
        transition: all .3s linear;
        color: transparent;
        cursor: pointer;

        &:hover{
            background: rgba(47,43,67,.5);
            color: black;
        }
    }
}
.pcs_3{

}
.pcs_4{
    width: 25rem;
}
.pcs_5{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-top: 1rem;
    
}
.pcs_6{
    font-size: 3rem;
    padding-left: 3.5rem;
}
.pcs_7{
    font-size: 1.5rem;
    padding-left: 3.5rem;
    padding-top: 1rem;
}
.pcs_8{
    position: absolute;
    left: 50%;
    top:50%;
    width: 80%;
    transform: translate(-50%,-50%);
    display:flex;
    flex-direction: column;
    font-size: 1.5rem;
    padding-top: 2rem;
    justify-content: center;
    // padding-left: 5rem;


    & input[type="text"] {
        width: 75%;
        border: none;
        border-bottom: 2px solid #393351;
        background: transparent;
        margin-bottom: 1.2rem;

    }
    & input[type="text"]:focus {
        outline: none;
    }
    & button {
        width: 75%;
        border: 1px solid #393351;
        background: transparent;
        margin-top: 1.2rem;
        transition: all .3s ease-in-out;
        border-radius: 1.4rem;
    }
    & button:hover{
        background: #393351;
        color: whitesmoke;
    }
    & button:focus{
        outline: none;
    }
    & .pcs_10{
        display: flex;
        flex-direction: column;
    }
}
.pcs_12{
    position: absolute;
    height: 65vh;
    width: 40vw;
    background: #2f2b43;
    border-radius: 1.4rem;
    left: 50%;
    top:13rem;
    transform: translateX(-50%);

    & input[type="file"]{
        display: none;
    }
    & .pcs_14{
        border-radius: 1.4rem;
        display: inline-block;
        padding: 6px 12px;
        cursor: pointer;
        font-size: 1.5rem;
        color: whitesmoke;
        background: #393351;
        transition: all .3s ease-in-out;
    }
    & .pcs_14:hover{
        background: whitesmoke;
        color: black;
    }
    & .pcs_14 label{
        cursor: pointer;
    }

    & .pcs_15{
        position: absolute;
        top: 3rem;
        left: 0;
        right: 0;
        bottom: 80px;
    }

 }
.pcs_13 {
    position: relative;
}
.pcs_16{
    margin-top: 6rem;
}